.person {
  padding: 10px 10px 16px;
  transition: transform 1s;
}

.person__photo {
  width: 200px;
  display: flex;
  position: relative;
}

.person__photo--rank {
  color: #8b0000;
  size: 16px;
  text-align: center;
  background-color: #fff;
  border-radius: 200px;
  align-content: center;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__photo--img {
  border-radius: 200px;
  width: 200px;
  transition: border-radius 1s;
}

.person__details {
  align-content: center;
  justify-items: center;
  display: block;
}

.person__details--name {
  color: #2f4f4f;
  text-align: center;
  size: 20px;
  margin: 8px 0 0;
  line-height: 23px;
  display: block;
}

.person__details--extra {
  size: 18px;
  text-align: center;
  visibility: hidden;
  margin: 9px 0 0;
  line-height: 21px;
  display: block;
}

.person__details--sport:after {
  content: ", ";
}

.person__details--pay:after {
  content: "M";
}

.person__details--pay:before {
  content: "$";
}

.person:hover {
  z-index: 10;
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .person__details--extra {
  visibility: visible;
}

.person:hover .person__photo--img {
  border-radius: 60px;
}

.people {
  grid-template-columns: repeat(2, 220px);
  justify-content: center;
  justify-items: center;
  gap: 20px 10px;
  margin: 0;
  padding: 20px 25px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

html {
  font-family: Arial, sans-serif;
}

body {
  margin: 0;
  padding: 0;
}
